// this follows the process described by bayesian causal networks 101: https://en.m.wikipedia.org/wiki/Bayesian_network#example

interface ProbL1 {
  [index: string]: number;
};
interface ProbL2 {
  [index: string]: ProbL1;
};
interface ProbL3 {
  [index: string]: ProbL2;
};

enum CausalLevel {
  high,
  low
}

const RainTF: ProbL1 = {
  T: 0.2,
  F: 0.8
};

const SprinklerTFCondRain: ProbL2 = {
  F: {T: 0.4, F: 0.6},
  T: {T: 0.01, F: 0.99}
};

const GrassTFCondSprinklerRain: ProbL3 = {
  F: {F: {T: 0, F: 1}, T: {F: 0.8, T: 0.2}},
  T: {F: {F: 0.9, T: 0.1}, T: {T: 0.99, F: 0.01}}
};

const GrassTFCondSprinklerRainLow: ProbL3 = {
  F: {F: {T: 0.3, F: 0.7}, T: {F: 0.5, T: 0.5}},
  T: {F: {F: 0.55, T: 0.45}, T: {T: 0.6, F: 0.4}}
};

function singleRun(causalLevel = CausalLevel.high) {
  // first we draw random draws for rain, T and F
  let rain = Math.random() < RainTF.T ? "T" : "F";
  // then conditioning on the rain we draw the sprinkler
  let sprinkler = Math.random() < SprinklerTFCondRain[rain].T ? "T" : "F";
  // then look up the sprinkler and rain probability and draw these probabilities
  let grassProb: ProbL3;
  if (causalLevel === CausalLevel.high) {
    grassProb = GrassTFCondSprinklerRain;
  } else {
    grassProb = GrassTFCondSprinklerRainLow;
  }

  let grass = Math.random() < grassProb[sprinkler][rain].T ? "T" : "F";
  // now return the instance
  return {
    rain,
    sprinkler,
    grass
  };
}

export function getData(runs: number) {
  let results = [];
  for (let i = 0; i < runs; i ++) {
    results.push(singleRun());
  }
  return results;
}


