import * as React from "react";

import Chart from "./Chart";
import { Data, Filter, Selection, countByFilter, Result, Nodes, AnonNodeNameMap, noSelection } from "../lib/helper";


interface ContainerProps {
  data: Data[];
  noSelectionValue: Result;
  anonimize?: Boolean;
}

interface ContainerState {
  selection: Selection;
  values: Result;
}


export default class Container extends React.Component<ContainerProps, ContainerState> {
  static defaultProps = {
    anonimize: false,
  };

  constructor(props: ContainerProps) {
    super(props);
    this.updateFilter = this.updateFilter.bind(this);
    this.reset = this.reset.bind(this);
    // we need to compute the initial value
    this.state = {
      selection: JSON.parse(JSON.stringify(noSelection)),
      values: this.props.noSelectionValue
    };
  }

  reset() {
    this.setState({
      selection: JSON.parse(JSON.stringify(noSelection)),
      values: this.props.noSelectionValue
    });
  }

  updateFilter(selection: string, filterValue: Filter) {
    let v = this;
    return () => {
      console.log("clicked", selection, filterValue);
      v.setState(prevState => {
        let newState = JSON.parse(JSON.stringify(prevState));
        // update filter
        newState.selection[selection] = filterValue;
        // also update the result
        newState.values = countByFilter(this.props.data, newState.selection);
        return newState;
      });
    };
  }
  render() {
    let vis: JSX.Element[] = [];
    for (let i = 0; i < Nodes.length; i ++) {
      let n = Nodes[i];
      vis.push(<Chart
        dataset={[{x: "true", y: this.state.values[n].T}, {x: "false", y: this.state.values[n].F}]}
        baselineDataset={[{x: "true", y: this.props.noSelectionValue[n].T}, {x: "false", y: this.props.noSelectionValue[n].F}]}
        click={this.updateFilter}
        filter={this.state.selection[n]}
        shownLabel={this.props.anonimize ? AnonNodeNameMap[n] : n}
        label={n}
      />);
    }
    return <div>

      {vis}
      <br/>
      <div className="middle">
        <button onClick={this.reset}>reset</button>
      </div>
    </div>;
  }
}


