import * as React from "react";

import Container from "./Container";
import CausalStructure from "./CausalStructure";
import { getData } from "../lib/dataGen";
import { AnonNodeNameMap, countByFilter, TotalSizeSmall, noSelection } from "../lib/helper";

enum Step {
  Vis,
  Semantics
}

interface PageContainerState {
  step: number;
}

const StepSequence = [Step.Vis, Step.Semantics];

let data = getData(TotalSizeSmall);
let noSelectionValue = countByFilter(data, noSelection);

export default class PageContainer extends React.Component<undefined, PageContainerState> {
  answer: CausalStructure;

  constructor(props: undefined) {
    super(props);
    this.goToNextStep = this.goToNextStep.bind(this);
    this.state = {
      step: 0,
    };
  }

  goToNextStep() {
    // this.answer.resetState();
    this.setState((prevState) => {
      return { step: prevState.step += 1 };
    });
  }

  render() {
    // get the mappings
    let pairs: [string, string][] = [];
    for (let k in AnonNodeNameMap) {
      pairs.push([k, AnonNodeNameMap[k]]);
    }
    let introDiv = (
      <div className="explain">
        <p>
          Below are three charts that visualizes the occurance of three instances of related events.  
          The gray bar in the background is the total count of the event without any filtering (the original condition), and the blue bar in the foreground is the total count of the event with the filtering.
          When the value is zero, we show a red bar below.
        </p>
        <p>
          For instance, when you click on the "true" bar for chart B, the bar you just clicked will turn into a darker blue, and you will see that some of the other bars will have part gray and part blue---this is because after the filtering by your interaction, the total count for the other values may have decreased.
        </p>
        <p>
          If you want to clear a selection---just click the "Clearing filtering by (selected)" at the bottom of the chart.
        </p>
      </div>);
    let moreInformationDiv: JSX.Element;
    if (this.state.step > 0) {
      moreInformationDiv = <div className="newInfo">
        <p>Now we disclose a bit more information---the events you just read actually corresponds to concrete events in the real world---{pairs.map(p => `${p[1]} is ${p[0]}`).join(" ,")}.</p>
        <p>Feel free to play around with the visualization (same data; only labels have changed) and update your answer if you feel there are any new changes.</p>
      </div>;
    }
    let anonimize = this.state.step === Step.Vis;
    return <div className="content-wrapper">
      {introDiv}
      <Container
        anonimize={anonimize}
        data={data}
        noSelectionValue={noSelectionValue}
      />
      {moreInformationDiv}
      <p>
        We ask you to share your insight from analyzing the results.  Specifically, we are interested in the relationships between the events.
      </p>
      <p>
        In the following diagram, click on the edge if you think there is some influence between the events.  In addition, if you observe that one event may inlfuence the other, please also click the gray arrow.
      </p>
      <CausalStructure
        ref={c => this.answer = c}
        anonimize={anonimize}
      />
      <button disabled={this.state.step === StepSequence.length - 1} onClick={this.goToNextStep}>Next</button>
    </div>;
  }
}


