import * as React from "react";
import { Edge, Nodes, EdgeDirection, getArrow, AnonNodeNameMap } from "../lib/helper";


interface CausalStructureProps {
  width?: number;
  height?: number;
  fontSize?: number;
  baseColor?: string;
  highlighColor?: string;
  radius?: number;
  arrowSize?: number;
  anonimize?: boolean;
}

interface CausalStructureState {
  activeEdges: Edge[];
}

export default class CausalStructure extends React.Component<CausalStructureProps, CausalStructureState> {
  static defaultProps = {
    radius: 30,
    annon: false,
    arrowSize: 10,
    fontSize: 20,
    height: 200,
    width: 400,
    baseColor: "#D3D3D3",
    highlighColor: "#66b266"
  };

  constructor(props: CausalStructureProps) {
    super(props);
    this.activateEdge = this.activateEdge.bind(this);
    this.state = {
      activeEdges: [],
    };
  }

  resetState() {
    this.setState({activeEdges: []});
  }

  activateEdge(edge: Edge) {
    let v = this;
    return () => {
      v.setState((prevState) => {
        let found = prevState.activeEdges.filter((e) => (e.from === edge.from) && (e.to === edge.to));
        if (found.length > 0) {
          found[0].direction = edge.direction;
        } else {
          prevState.activeEdges.push(edge);
        }
        return prevState;
     });
    };
  }

  render() {
    const { arrowSize, width, height, fontSize, baseColor, highlighColor, radius, anonimize } = this.props;
    let nodePos = Nodes.map((n, i) => {
      return {
        x: width * 0.6 / (Nodes.length) * (i + 1) + 20,
        y: i % 2 * height * 0.6 + 50
      };
    });
    const fill = "gray";
    const totalNodes = Nodes.length;
    let nodes: JSX.Element[] = Nodes.map((n, i) =>
      (<g>
        <circle cx={nodePos[i].x} cy={nodePos[i].y} r={radius} fill={fill}></circle>
        <text x={nodePos[i].x} y={nodePos[i].y + 2 * radius} fill={"#b26675"} fontSize={fontSize} fontWeight={"bold"}>{anonimize ? AnonNodeNameMap[n] : n}</text>
      </g>)
    );
    let lines: JSX.Element[] = Nodes.map((n, i) => {
      let from = i;
      let fromPosRaw = nodePos[from];
      let to = (i + 1) % totalNodes;
      let toPosRaw = nodePos[to];
      let fromPos = {
        x: (5 * fromPosRaw.x + toPosRaw.x) / 6,
        y: (5 * fromPosRaw.y + toPosRaw.y) / 6
      };
      let toPos = {
        x: (fromPos.x + 5 * toPosRaw.x) / 6,
        y: (fromPos.y + 5 * toPosRaw.y) / 6
      };
      // see if it's active
      let activeEdge = this.state.activeEdges.filter((e) => (e.from === from) && (e.to === to));
      let color = activeEdge.length ? highlighColor : baseColor;
      let arrows: JSX.Element[] = [];
      let fromTri = getArrow(fromPos.x, fromPos.y, arrowSize).map(a => `${a.x}, ${a.y}`).join(" ");
      let toTri = getArrow(toPos.x, toPos.y, arrowSize).map(a => `${a.x}, ${a.y}`).join(" ");
      let fromColor = baseColor;
      let toColor = baseColor;
      if (activeEdge.length) {
        fromColor = activeEdge[0].direction === EdgeDirection.backward ? highlighColor : baseColor;
        toColor = activeEdge[0].direction === EdgeDirection.forward ? highlighColor : baseColor;
      }
      arrows.push(<polygon
        points={fromTri}
        fill={fromColor}
        className="arrow"
        onClick={this.activateEdge({from, to, direction: EdgeDirection.backward})}
      />);
      arrows.push(<polygon
        points={toTri}
        fill={toColor}
        className="arrow"
        onClick={this.activateEdge({from, to, direction: EdgeDirection.forward})}
      />);

      return <g>
        <line
          x1={fromPos.x} x2={toPos.x} y1={fromPos.y} y2={toPos.y}  strokeWidth={5} stroke={color}
          className="arrow"
          onClick={activeEdge.length ? () => {} : this.activateEdge({from, to, direction: EdgeDirection.none})}
        />
        {arrows}
      </g>;
    });
    return (<div>
      <svg width={width + 40} height={height + 40}>
        {nodes}
        {lines}
      </svg>
    </div>);
  }
}



