export interface Datum {
  x: string;
  y: number;
}

export enum Filter {
  true,
  false,
  none
};

export const noSelection = {
  rain: Filter.none,
  sprinkler: Filter.none,
  grass: Filter.none
};


export const TotalSizeSmall = 50;
export const TotalSizeLarge = 100;

// ugh
export const FilterMap: {[index: string]: Filter} = {
  "true": Filter.true,
  "false": Filter.false,
  "none": Filter.none
};

export const Nodes = ["rain", "sprinkler", "grass"];

export enum EdgeDirection {
  forward,
  backward,
  none
}

export function getArrow(x: number, y: number, size = 20) {
  return [{x: x - size, y: y - size}, {x: x, y: y + size}, {x: x + size, y: y - size}];
}

// the number refers to the offset in the Nodes
export interface Edge {
  from: number;
  to: number;
  direction: EdgeDirection;
}

export const AnonNodeNameMap: {[index: string]: string} = {
  "rain": "A",
  "sprinkler": "B",
  "grass": "C"
};

export interface Selection {
  [index: string]: Filter;
};

export interface Data {
  // rain: string;
  // sprinkler: string;
  // grass: string;
  [index: string]: string;
}

export interface Result {
  [index: string]: {
    T: number,
    F: number
  };
}

