// we need to do filter by two
export function countByFilter(data: Data[], filter: Selection) {
  // now we count
  console.log("filtering", JSON.stringify(filter), "data", data);
  let counts: Result = {};
  for (let j = 0; j < Nodes.length; j ++) {
    let r: Data[] = data;
    // this is the crossfiltering step, where the thing itself is not filtered by...
    for (let i = 0; i < Nodes.length; i ++) {
      if (i !== j) {
        if (filter[Nodes[i]] === Filter.true) {
          r = r.filter((d) => d[Nodes[i]] === "T");
        } else if (filter[Nodes[i]] === Filter.false) {
          r = r.filter((d) => d[Nodes[i]] === "F");
        }
      }
    }
    counts[Nodes[j]] = {
      T: (r.filter(d => d[Nodes[j]] === "T").length),
      F: (r.filter(d => d[Nodes[j]] === "F").length)
    };
  }
  console.log("counts", counts);
  return counts;
}

// hack, oh well
interface CustomColorScale {
  [index: string]: string;
}

export const defaultColorScale: CustomColorScale = {
  0: d3ScaleChromatic.schemeSet1[1],
  1: d3ScaleChromatic.schemeSet1[3],
  2: d3ScaleChromatic.schemeSet1[4],
};


